* {
  border: 0;
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  background: rgb(2, 0, 36);
  background: linear-gradient(
    180deg,
    rgba(2, 0, 36, 1) 0%,
    rgba(5, 117, 62, 1) 73%,
    rgba(9, 121, 50, 1) 98%,
    rgba(28, 209, 88, 1) 100%
  ) no-repeat center center/cover;

  font-size: 24px;
  font-family: 'Anton', sans-serif;
}

.app {
  width: 100%;
  height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  padding-left: .64em;
  padding-right: .64em;
  padding-top: 80px;
}

button {
  padding: .64em;
  border-radius: 0.25em;
  text-transform: uppercase;
  font-weight: bold;
  box-shadow: 1px 2px 5px rgba(0,0,0,.3);
  margin-top: 20px;
  margin-bottom: 20px;
  cursor: pointer;
  background-color: #e8f8ba;
  transition: background .1s linear;
}

button:hover {
  background-color: #76bd55;
}

button:hover, button:focus {
  outline: none;
}

img {
  width: 100%;
  max-width: 16.6em;
  padding: .64em;
  border-radius: .64em;
}

p {
  font-size: 48px;
  color: #ccc;
  text-align: center;
  animation: fade .2s;
}

span {
  font-size: .75em;
  color: #bbb;
  color: #daa646;
  text-transform: capitalize;
}

@media(min-width: 680px) {
  .app {
    padding-left: 10.24em ;
    padding-right: 10.24em ;
  }
}

@keyframes fade {
  0% { opacity: 0; }
  100% { opacity: 1; }
}
